.services{
    background: hsl(263, 100%, 5%);
    perspective: 1mm;
    .card{
        border-radius: 0;
        border-top-right-radius: 1.8rem;

        &:hover{
            @include hoverEffects;

            i{
                background: $secondary !important;
                color: $primary;
            }
        }
        

        .card-header{
            margin-top: -2.5rem;
            border: none;
        }

        i{
            border-radius: 0.5rem;
        }
    }
}